/*	 TOPBAR
/*---------------------------------------------------*/

.kura_tm_topbar {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 10;
  /* transform: translateY(-100%); */
  transition: all 0.4s ease;
}
body.opened .kura_tm_topbar {
  transition-delay: 0.4s;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.kura_tm_topbar .wrapper {
  width: 100%;
  height: auto;
  clear: both;
  float: left;
  transition: all 0.3s ease;
}
.kura_tm_topbar.animate .wrapper {
  background-color: rgba(255, 255, 255, 1);
}
.kura_tm_topbar .topbar_inner {
  width: 100%;
  height: auto;
  clear: both;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 37px 30px 50px;
  transition: all 0.3s ease;
}
.kura_tm_topbar.animate .topbar_inner {
  padding: 20px 37px 20px 50px;
}
.kura_tm_topbar .logo img {
  max-width: 70px;
  max-height: 70px;
}
.kura_tm_topbar .menu {
  display: flex;
  align-items: center;
}
.kura_tm_topbar .list {
  padding-right: 40px;
}
.kura_tm_topbar .list ul {
  margin: 0px;
  list-style-type: none;
}
.kura_tm_topbar .list ul li {
  margin: 0px 20px 0px 0px;
  display: inline-block;
  list-style-type: none;
  opacity: 0;
  visibility: hidden;
  position: relative;
  left: 10px;
  transition: all 0.3s ease;
}
.kura_tm_topbar .list ul li:last-child {
  margin-right: 0px;
}
.kura_tm_topbar .list ul li.opened {
  opacity: 1;
  visibility: visible;
  left: 0px;
}
.kura_tm_topbar .list ul li a {
  text-decoration: none;
  color: #000;
  font-family: 'Poppins';
  transition: all 0.3s ease;
}
.kura_tm_topbar .list ul li a:hover {
  color: #ff4522;
}
.kura_tm_topbar .list ul li.current a {
  color: #ff4522;
}
.kura_tm_topbar .trigger {
  line-height: 1;
}
.trigger .hamburger-inner,
.trigger .hamburger-inner:after,
.trigger .hamburger-inner:before {
  height: 2px;
  width: 30px;
}
.trigger .hamburger {
  padding: 0px;
}

.trigger {
  display: flex;
}


@media (max-width: 1040px) {
  .kura_tm_topbar {
    display: none;
  }

}

